header {
  height: 4rem;
  display: flex;
  padding: 0 2%;
}

.Navbar {
  display: flex;
  width: 90%;
}
.logo {
  width: 10%;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.logo img {
  height: 30px;
  margin-right: 0.5em;
}

.searchForm {
  align-items: center;
  display: flex;
  margin: auto;
  width: 50%;
  height: 2rem;
}

.input {
  width: 100%;
  height: 100%;
  outline: 0;
  padding-left: 0.5em;
}
.searchForm button {
  color: darkgray;
  outline: 0;
  cursor: pointer;
}
.searchImg {
  width: 24px;
  height: 100%;
}

@media screen and (max-width: 1023px) {
  .searchForm {
    width: 50%;
  }
}
